body {
    padding-bottom: 54px;
    background: lighten($color: $light-shades, $amount: 0%); // $light-shades;
    font-family: 'Open Sans', sans-serif;
}

a {
    color: $main-brand;
    text-decoration: none;
    transition: all 0.3s;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}

a:hover,
a:focus {
    color: $light-accent;
    text-decoration: none;
    transition: all 0.3s;
    font-weight: bold;
}

.bg-dark {
    background-color: $dark-accent !important;
}

.btn-outline-primary {
    color: $main-brand;
    background-color: transparent;
    background-image: none;
    border-color: $main-brand;
}

.btn-outline-primary:hover,
.btn-outline-primary:focus,
.btn-outline-primary.focus {
    color: $light-accent;
    background-color: transparent;
    border-color: $light-accent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Halant', serif;
  color: $dark-accent;
}

ol.space_list li { 
    margin-bottom: 1em; 
    color: $dark-shades;
}

.navbar {
    background: $dark-accent !important;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}

p {
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5em;
    color: $dark-shades;
    text-align: justify;
}

.sidebar {
  background-color: $dark-accent;
}

.sidebar .nav-link {
    color: $dark-accent;
}

.sidebar .nav-link.active {
    background-color: lighten($color: $main-brand, $amount: 15%);
    color: $light-shades;
}

.sidebar .nav-link:hover, 
.sidebar .nav-link:focus {
    color: $light-accent;
}

.sidebar ul li {
    font-size: 0.9em;
    line-height: 1em;
}

